.main-content-text {
  max-width: 1200px;
  margin: 3rem auto;
  padding: 2rem;
  background: var(--card-bg);
  border-radius: 1.5rem;
  box-shadow: 0 6px 16px var(--shadow);
  text-align: center;
}

.main-content-text h3 {
  color: var(--primary);
  margin-bottom: 1rem;
}

textarea,
input[type='text'] {
  width: 100%;
  padding: 1rem;
  border: 1px solid #ccc;
  border-radius: 0.75rem;
  font-size: 1rem;
  margin-bottom: 1rem;
  background: white;
  color: #333;
}

button {
  background: var(--primary);
  color: white;
  border: none;
  padding: 0.7rem 1.4rem;
  font-size: 1rem;
  border-radius: 0.5rem;
  cursor: pointer;
  transition: background 0.3s;
}

button:hover {
  background: var(--secondary);
}
